import React from 'react';
import s from './Post.module.css';

const Post = (props) => {

    return (
        <div className={s.item}>
            <img src='https://d1.awsstatic.com/logos/customers/ifunny-logo.7d6dd876e74aa9d4da4823f9a8b8d79b949bbf1f.png'></img>
            {props.message}
                <div>
                <span>like 
                </span>
                {props.like}
            </div>
        </div>

    )
}

export default Post;
