import React, {useState} from 'react';
import s from './ProfileInfo.module.css';
import Preloader from "../../common/Preloader/Preloader";
import ProfileStatusWithHooks from "./ProfileStatusWithHooks";
import userPhoto from '../../../assets/images/images.jpg';
import ProfileDataForm from "./ProfileDataForm";

const ProfileInfo = ({profile, savePhoto, isOwner,updateStatus,status, saveProfile }) => {
    let [editMode, setEditMode] = useState(false);
    if(!profile){
        return <Preloader />
    }
    const onMainPhotoSelected = (e) => {
        if(e.target.files.length){
            savePhoto(e.target.files[0]);
        }
    }
    const onSubmit = (formData) => {
        saveProfile(formData).then(
            () => {
                setEditMode(false);
            }
        )

    }
    return (
        <div>
            <div className={s.descriptionBlock}>
                <img src={profile.photos.large || userPhoto} className={s.mainPhoto}/>
                <div className={s.inputWrapper}>
                {isOwner && <input className={s.editPhoto} type={'file'}  onChange={onMainPhotoSelected}/>}
                </div>
                <div className={s.formDataModule}>
                {editMode
                    ? <ProfileDataForm initialValues={profile} profile={profile} onSubmit={onSubmit}/>
                    : <ProfileData profile={profile}
                                   isOwner={isOwner}
                                   goToEditMode={() => {setEditMode(true)}}/>}
                </div>
                <ProfileStatusWithHooks status={status} updateStatus={updateStatus}/>


        </div>
        </div>
    )
}
const ProfileData = ({profile,isOwner, goToEditMode}) => {
  return  <div>
      {isOwner && <button onClick={goToEditMode}>edit</button>}
        <div>
            <b>Full Name:</b> {profile.fullName}
        </div>
        <div>
            <b>Looking For a job:</b> {profile.lookingForAJob ? "yes" : "no"}
        </div>
        {profile.lookingForAJob &&
        <div>
            <b>My skills</b> {profile.lookingForAJobDescription}
        </div>
        }
        <div>
            <b>About me:</b> {profile.aboutMe}
        </div>
        <div>
            <b>Contacts:</b> {Object.keys(profile.contacts).map(key =>{
            return <Contact key={key} contactTitle={key} contactValue={profile.contacts[key]} />
        })}
        </div>

    </div>
}

const Contact = ({contactTitle, contactValue}) => {
    return <div className={s.contact}>
        <b>{contactTitle}</b> : {contactValue}
    </div>
}

export default ProfileInfo;
